let clock = null;
let state = 0;
let speed = 6;
let currentScore = 0



window.onload = () => {
    const startButton = $('.start')
    startButton.onclick = start
}

// å·¥å…·å°è£…
// æ ¹æ®idæ¥è·å–å…ƒç´ 
function $(node) {
    return document.querySelector(node);
}

// åˆ›å»ºdiv, classNameæ˜¯å…¶ç±»å
function creatediv(className) {
    var div = document.createElement('div');
    div.className = className;
    return div;
}

//ç‚¹å‡»å¼€å§‹æ¸¸æˆæŒ‰é’® å¼€å§‹æ¸¸æˆ
function start() {
    $('.start').classList.add('hidden')

    for (var i = 0; i < 4; i++) {
        createrow();
    }

    // æ·»åŠ onclickäº‹ä»¶
    $('main').onclick = function (ev) {
        ev = ev || event
        judge(ev);
    }

    // å®šæ—¶å™¨ æ¯30æ¯«ç§’è°ƒç”¨ä¸€æ¬¡move()
    clock = window.setInterval(move, 30);
}


// åˆ¤æ–­æ˜¯å¦ç‚¹å‡»é»‘å—ã€ç™½å—
function judge(ev) {
    if (ev.target.className.indexOf('black') === -1 && ev.target.className.indexOf('cell') !== -1) {
        ev.target.parentNode.pass1 = 1; //å®šä¹‰å±æ€§pass1ï¼Œè¡¨ç¤ºæ­¤è¡Œrowçš„ç™½å—å·²ç»è¢«ç‚¹å‡»
    }

    if (ev.target.className.indexOf('black') !== -1) {
        //ç‚¹å‡»ç›®æ ‡å…ƒç´  ç±»åä¸­åŒ…å« black è¯´æ˜æ˜¯é»‘å—
        ev.target.className = 'cell';
        // TODO
        ev.target.parentNode.pass = 1; //å®šä¹‰å±æ€§passï¼Œè¡¨æ˜æ­¤è¡Œrowçš„é»‘å—å·²ç»è¢«ç‚¹å‡»
        score();
    }


}


// æ¸¸æˆç»“æŸ
function fail() {
    clearInterval(clock);
    confirm(`ä½ çš„æœ€ç»ˆå¾—åˆ†ä¸º${currentScore}`);
    $('.start').classList.remove('hidden')
    var con = $('#con');
    con.innerHTML = "";
    $('#score').innerHTML = 0;
    con.style.top = '-408px';
}

// åˆ›é€ ä¸€ä¸ª<div class="row">å¹¶ä¸”æœ‰å››ä¸ªå­èŠ‚ç‚¹<div class="cell">
function createrow() {
    var con = $('#con');
    var row = creatediv('row'); //åˆ›å»ºdiv className=row
    var arr = creatcell(); //å®šä¹‰div cellçš„ç±»å,å…¶ä¸­ä¸€ä¸ªä¸ºcell black

    con.appendChild(row); // æ·»åŠ rowä¸ºconçš„å­èŠ‚ç‚¹

    for (var i = 0; i < 4; i++) {
        row.appendChild(creatediv(arr[i])); //æ·»åŠ rowçš„å­èŠ‚ç‚¹ cell
    }

    if (con.firstChild == null) {
        con.appendChild(row);
    } else {
        con.insertBefore(row, con.firstChild);
    }
}


// åˆ›å»ºä¸€ä¸ªç±»åçš„æ•°ç»„ï¼Œå…¶ä¸­ä¸€ä¸ªä¸ºcell black, å…¶ä½™ä¸ºcell
function creatcell() {
    var temp = ['cell', 'cell', 'cell', 'cell',];
    var i = Math.floor(Math.random() * 4);//éšæœºäº§ç”Ÿé»‘å—çš„ä½ç½® Math.random()å‡½æ•°å‚æ•° 0~1çš„éšæœºæ•°
    temp[i] = 'cell black';
    return temp;
}

//è®©é»‘å—åŠ¨èµ·æ¥
function move() {
    let con = $('#con');
    let top = parseInt(window.getComputedStyle(con, null)['top']);

    if (speed + top > 0) {
        top = 0;
    } else {
        top += speed;
    }

    con.style.top = top + 'px';//ä¸æ–­ç§»åŠ¨topå€¼ï¼Œä½¿å®ƒåŠ¨èµ·æ¥
    over();
    if (top === 0) {
        createrow();
        con.style.top = '-102px';
        delrow();
    }
}

// åˆ¤æ–­æ¸¸æˆæ˜¯å¦ç»“æŸ
function over() {
    let rows = con.childNodes;
    if ((rows.length == 5) && (rows[rows.length - 1].pass !== 1)) {
        fail();
    }
    for (let i = 0; i < rows.length; i++) {
        if (rows[i].pass1 === 1) {
            fail();
        }
    }
}




// åŠ é€Ÿå‡½æ•°
function speedup() {
    speed += 2;
    if (speed == 20) {
        alert('ä½ è¶…ç¥äº†');
    }
}

//åˆ é™¤æŸè¡Œ
function delrow() {
    var con = $('#con');
    if (con.childNodes.length == 6) {
        con.removeChild(con.lastChild);
    }
}

// è®°åˆ†
function score() {
    currentScore += 1
    $('#score').innerHTML = currentScore;//ä¿®æ”¹åˆ†æ•°
    if (currentScore % 10 == 0) {//å½“åˆ†æ•°æ˜¯10 çš„å€æ•°æ—¶ä½¿ç”¨åŠ é€Ÿå‡½æ•°ï¼Œè¶Šæ¥è¶Šå¿«
        speedup();
    }
}